import React from "react"

const style = {
  width: "700px",
  marginLeft: "auto",
  marginRight: "auto",
  display: "block",
  textAlign: "center",

}

const Logo = props =>
  <div>
    <img style={style} src="blessedsacrement.png" />
  
  </div>;

export default Logo;



